:root {
  --main-bg-color: #fefefe;
  --main-txt-color: #2c2c2c;
  --yellow-main: #ffe550;
  --button-green: #bbf330;
  --button-green-hover: #b5ff00;
  --dark-green: #7eb101;
  --dark-green-hover: #597d00;
  --pure-white: #fff;
  --accent: #ff4d8d;
  --green: #a7ce47;
  --pure-black: #000;
  --dark-grey: #505050;
  --warning-color: #ff705d;
  --shadow-fruit: radial-gradient( 50% 50% at 50% 50%, #2f583f 0%, rgba(105, 105, 105, 0) 100% );
  --cubic: 250ms cubic-bezier(.4, 0, .2, 1);
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: var(--main-bg-color);
  color: var(--main-txt-color);
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
}

button {
  cursor: pointer;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  max-width: 480px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
