    margin-bottom: 4px;
  }
}

.form-subscribe__input {
  min-width: 279px;
  height: 100%;
  color: var(--dark-grey);
  transition: border var(--cubic);
  border-radius: 30px;
  padding: 13px;
  font-size: 12px;
}

.form-subscribe__input:focus-visible {
  outline: none;
}

.form-subscribe__input:-ms-placeholder-shown {
  border: 1px solid var(--yellow-main);
}

.form-subscribe__input:placeholder-shown {
  border: 1px solid var(--yellow-main);
}

.form-subscribe__input:not(:placeholder-shown):valid {
  border: 1px solid var(--dark-green);
}

.form-subscribe__input:not(:placeholder-shown):invalid {
  border: 1px solid var(--warning-color);
}

.form-subscribe__input:not(:placeholder-shown):invalid + .form-subscribe__msg {
  opacity: 1;
}

.form-subscribe__msg {
  color: var(--warning-color);
  opacity: 0;
  transition: opacity var(--cubic);
  align-items: center;
  gap: 4px;
  font-size: 10px;
