.backdrope {
  height: 100vh;
  width: 100vw;
  opacity: 1;
  z-index: 10;
  transition: opacity var(--cubic), transform var(--cubic);
  background-color: rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
  left: 0;
  transform: scale(1);
}

